<template><section>
<h1>number 案例</h1>
<hr>
<h3>基础用法</h3>
<div class="demo-block">
    <in-number v-model="value1"></in-number>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-number v-model="value"></in-number>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">in-number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-number</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data(){
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">value</span>:<span class="hljs-number">10</span>
        }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>禁用状态</h3>
<div class="demo-block">
    <in-number disabled v-model="value3"></in-number>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-number disabled v-model="value"></in-number>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">in-number</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-number</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data(){
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">value</span>:<span class="hljs-number">10</span>
        }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>步长、最大值、最小值</h3>
<div class="demo-block">
    <in-number v-model="value2" :min="0" :max="100" :step="5"></in-number>
</div>
<demo-block>
                        <div slot="desc"><html><head></head><body><in-number v-model="value2" :min="0" :max="100" :step="5"></in-number>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">in-number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"0"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"100"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"5"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">in-number</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data(){
        <span class="hljs-keyword">return</span> {
            <span class="hljs-attr">value</span>:<span class="hljs-number">10</span>
        }
    }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>组件属性</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>booblean</td>
<td>true/false</td>
<td>false</td>
</tr>
<tr>
<td>value(v-model)</td>
<td>输入框的值</td>
<td>string/number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>min</td>
<td>最小数</td>
<td>number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>max</td>
<td>最大值</td>
<td>number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>step</td>
<td>加减步长</td>
<td>number</td>
<td>-</td>
<td>1</td>
</tr>
</tbody>
</table>
<h3>组件事件</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>参数回调</th>
</tr>
</thead>
<tbody>
<tr>
<td>focus</td>
<td>组件获取焦点时触发</td>
<td>event</td>
</tr>
<tr>
<td>blur</td>
<td>组件失去焦点时触发</td>
<td>event</td>
</tr>
<tr>
<td>change</td>
<td>组件值改变时触发</td>
<td>event</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
    data(){
        return {
            value1:10,
            value2:10,
            value3:10
        }
    }
};
</script>


